<template>
  <div>
        <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="150px">
            <el-row>
                <el-col :span="22">
                    <el-form-item label="菜单代码" prop="menuCode">
                        <el-input type="text" v-model="dataForm.menuCode" maxlength='15' placeholder="请输入菜单代码"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="菜单名称" prop="menuName">
                        <el-input type="text" v-model="dataForm.menuName" maxlength='10' placeholder="请输入菜单名称"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="菜单图标" prop="menuIco">
                        <el-input type="text" v-model="dataForm.menuIco" placeholder="请输入菜单图标"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
             <el-row>
                <el-col :span="22">
                    <el-form-item label="菜单路径" prop="menuPath">
                        <el-input type="text" v-model="dataForm.menuPath" placeholder="请输入菜单路径"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="区域状态" prop="menuStatus">
                        <el-radio-group v-model="dataForm.menuStatus">
                            <el-radio :label="0">停用</el-radio>
                            <el-radio :label="1">启用</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <div class="dialog-footer">
                <el-button type="primary" @click="saveArea()">保存</el-button>
                <el-button type="danger" @click="cancle()">取消</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default{
        name:'add-area-dialog',
        props: [
                'checkedData',
                'isEdit'//true：添加，false 编辑
        ],
        data(){
            return{
                //表单验证
                rules: {
                    menuCode: [{
                        required: true,
                        message: '请输入菜单代码',
                        trigger: 'change'
                    }],
                    menuName: [{
                        required: true,
                        message: '请输入菜单名称',
                        trigger: 'change'
                    }],
                    menuPath: [{
                        required: true,
                        message: '请输入菜单路径',
                        trigger: 'change'
                    }],
                    menuIco: [{
                        required: true,
                        message: '请输入菜单图标',
                        trigger: 'change'
                    }],
                },
                dataForm:{
                    menuCode: "",
                    menuName: "",
                    menuPath: "",
                    menuIco:'',
                    menuStatus:0, 
                    sysId:'',  //系统id
                    menuPid:''  //菜单id
                },
               
            }
        },
        created(){ 
            this.setFormData()
        },
        methods:{
            setFormData(){
                if(this.isEdit === false){
                    this.dataForm.menuId = this.checkedData.menuId
                    this.dataForm.menuCode = this.checkedData.menuCode
                    this.dataForm.menuName = this.checkedData.menuName
                    this.dataForm.menuPath = this.checkedData.menuPath
                    this.dataForm.menuStatus = Number(this.checkedData.menuStatus)
                    this.dataForm.sysId = this.checkedData.sysId
                    this.dataForm.menuPid = this.checkedData.menuPid
                    this.dataForm.menuIco = this.checkedData.menuIco
                }else{
                    this.dataForm.sysId = this.checkedData.sysId
                    this.dataForm.menuPid = this.checkedData.menuPid
                } 
            },
            //保存
            saveArea(){
                console.log(this.dataForm)
                this.$refs['addForm'].validate((valid) => {
                    if(valid) {
                        this.$http({
                            url: this.$http.adornUrl(this.API.savaOrUpdateMenu),
                            method: 'post',
                            data: this.$http.adornData(
                                this.dataForm
                            )
                        }).then((res) =>{
                            var resData = res.data;
                            if(resData.code == 0) {
                                this.$message({
                                    message: '成功',
                                    type: 'success'
                                });
                                this.$emit("addback",true);
                            } else {
                                this.$message.error(resData.msg);
                            }
                        })
                    } 
                })
            },
            //取消
            cancle() {
                this.$emit("addback",false);
            } 
        }
    }
</script>

<style>
</style>



