<template>
  <el-container class="menu-administrator">
    <el-aside width='274px'>
      <!--<el-scrollbar style='height: 100%;'>-->
      <div :style="{height:siteContentViewHeight -10 + 'px'}">
        <org-tree ref="tree" :treeData="treeData" @node-click="orgTreeClick"></org-tree>
      </div>
      <!--</el-scrollbar>-->
    </el-aside>
    <el-main>
      <div class="hed-btn">
        <el-button type="primary" size="small" @click="addMenu()">添加</el-button>
        <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
        <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
        <el-button type="primary" size="small" @click="sortMenu()">保存排序</el-button>
      </div>
      <el-table :data="dataList" v-loading="dataListLoading" border :height="siteContentViewHeight - 100 +'px'" style="width: 100%;" @selection-change="selectionChangeHandle">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column label="序号" align="center" width="50">
          <template slot-scope="scope"> {{pageObj.pageIndex*pageObj.pageSize+scope.$index+1}} </template>
        </el-table-column>
        <el-table-column prop="menuName" label="菜单名称"></el-table-column>
        <el-table-column prop="menuCode" label="菜单代码"></el-table-column>
        <el-table-column prop="menuPath" label="菜单路径"></el-table-column>
        <el-table-column label="区域状态">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.menuStatus == 0" size="small" type="danger">停用</el-tag>
            <el-tag v-else size="small">启用</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" plain @click="editMenu(scope.$index,scope.row)">修改</el-button>
            <el-button type="danger" size="mini" plain @click="deleMenu(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div>
        <pagination :pageIndex="pageObj.pageIndex" :total="pageObj.total" :pageSizes="[5, 10, 20, 30]" :pageSize="pageObj.pageSize" @PageTurning="PageTurning"></pagination>
      </div>
    </el-main>
    <!-- 添加、修改弹框 -->
    <el-dialog :title="title" width="50%" :visible.sync="addMenuVisiable" v-if='addMenuVisiable' :close-on-click-modal="false" :modal-append-to-body='false' append-to-body>
      <addmenudialog @addback='addback' :checkedData="checkedData" :isEdit="isEdit"></addmenudialog>
    </el-dialog>
  </el-container>
</template>

<script>
import { swapItems } from '@/utils';
import orgTree from "@/components/baseComponents/orgTree.vue";
import pagination from '@/components/baseComponents/Pagination.vue'
import addmenudialog from './add-menu-dialog.vue'
export default {
  name: 'menu-administrator-index',
  props: ['siteContentViewHeight'],
  components: {
    orgTree,
    pagination,
    addmenudialog
  },
  data () {
    return {
      treeData: [],
      dataList: [],
      addMenuVisiable: false,
      dataListLoading: false,
      isEdit: '', //true 添加，false：修改
      checkedData: {},  //机构当前选中行
      nodeId: '',   //选中机构id
      sysId: '',//系统id
      tag: '',//选中的是否是系统
      tableDataSelections: [],
      pageObj: {
        pageIndex: this.API.constObj.pageIndex, //当前页码
        pageSize: this.API.constObj.pageSize, //页大小
        total: 0, //数据总数
      },
    }
  },
  created () {
    this.getDataTree()
  },
  methods: {
    getDataTree () {//获取机构
      this.$http({
        url: this.$http.adornUrl(this.API.getSysMenuTreeInfoMenu),
        method: 'post',
        data: this.$http.adornData()
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.treeData = data.data;
          this.orgTreeClick(this.treeData[0])
        } else {
          this.$message.error(data.msg);
          this.treeData = [];
        }
      });
    },
    //机构树点击事件
    orgTreeClick (data) {
      this.nodeId = data.id;
      this.sysId = data.sysId;
      this.tag = data.tag;
      if (this.tag === "system") {
        this.getDataList();
      } else {
        this.getData();
      }
    },
    //点击系统获取数据
    getDataList () {
      this.dataListLoading = true;
      this.$http({
        url: this.$http.adornUrl(this.API.getSysMenuInfoMenu),
        method: 'post',
        data: this.$http.adornData({
          sysId: this.nodeId,
          menuPid: 0,
          size: this.pageObj.pageSize,
          current: this.pageObj.pageIndex + 1,
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.dataList = data.data.records;
          this.pageObj.total = data.data.total;
        } else {
          this.$message.error(data.msg);
        }
        this.dataListLoading = false;
      });
    },
    //点击菜单获取数据
    getData () {
      this.dataListLoading = true;
      this.$http({
        url: this.$http.adornUrl(this.API.getSysMenuInfoMenu),
        method: 'post',
        data: this.$http.adornData({
          menuPid: this.nodeId,
          size: this.pageObj.pageSize,
          current: this.pageObj.pageIndex + 1,
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.dataList = data.data.records;
          this.pageObj.total = data.data.total;
        } else {
          this.$message.error(data.msg);
        }
        this.dataListLoading = false;
      });
    },
    //添加
    addMenu () {
      if (this.tag === "system") {
        this.checkedData.sysId = this.nodeId;
        this.checkedData.menuPid = '';
      } else {
        this.checkedData.menuPid = this.nodeId;
        this.checkedData.sysId = this.sysId;
      }
      this.title = "添加";
      this.isEdit = true;
      this.addMenuVisiable = true;
    },
    //编辑
    editMenu (index, row) {
      this.checkedData = row;
      this.title = "编辑";
      this.isEdit = false;
      this.addMenuVisiable = true;
    },

    //删除
    deleMenu (index, row) {
      this.$confirm('确定删除选中记录?', '操作提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http({
          url: this.$http.adornUrl(this.API.deleteMenu),
          method: 'post',
          data: this.$http.adornData({
            menuId: row.menuId
          })
        }).then(({ data }) => {
          if (data.code === 0) {
            this.getDataList();
            this.$message.success('删除成功');
          } else {
            this.$message.error(data.msg);
          }
        })
      }).catch(() => {

      });
    },

    selectionChangeHandle (val) {//多选
      this.tableDataSelections = val;
    },
    moveDirection (direction) { //移动
      if (this.tableDataSelections.length === 0) {
        this.$confirm('请选择一条数据！', '提示', {
          confirmButtonText: '确定',
          showCancelButton: false,
          type: 'warning',
          center: true
        });
        return;
      };
      var that = this, index;
      that.tableDataSelections.sort((prev, next) => {
        return that.dataList.indexOf(prev) - that.dataList.indexOf(next)
      })
      if (direction == 'down') that.tableDataSelections.reverse()
      that.tableDataSelections.forEach(item => {
        index = that.dataList.indexOf(item); //得到右侧选中值的标识坐标
        if (direction == 'up') that.moveUp(index);
        else if (direction == 'down') that.moveDown(index)
      })
    },
    moveUp (index) {//上移
      if (index == 0) return;
      this.dataList = swapItems(this.dataList, index, index - 1, 'up');
    },
    moveDown (index) { //下移
      if (index == this.dataList.length - 1) return;
      this.dataList = swapItems(this.dataList, index, index + 1, 'down');
    },
    sortMenu () {//保存排序
      var sortArr = [];
      if (this.dataList.length < 0) return;
      this.dataList.forEach((item, index) => {
        sortArr.push({
          id: item.menuId,
          orderno: index + 1
        })
      })
      this.$http({
        url: this.$http.adornUrl(this.API.sysMenuSortMenu),
        method: 'post',
        data: this.$http.adornData({
          data: JSON.stringify(sortArr)
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.getDataList()
          this.$message.success('操作成功');
        } else {
          this.$message.error(data.msg);
        }
      });
    },

    // 添加或修改组件回调方法--子组件回调数据的方法
    // @param {Boolean} isRefresh是否需要刷新数据 true=刷新；false=不刷新
    addback (isRefresh) {
      this.addMenuVisiable = false;
      if (isRefresh) {
        this.getDataList();
        this.getDataTree();
      }
    },

    //分页组件回调方法
    PageTurning (page_obj) {
      this.pageObj.pageIndex = page_obj.page_index;
      this.pageObj.pageSize = page_obj.page_size;
      this.getDataList();
    },
  }
}
</script>

<style lang="scss" scoped>
.menu-administrator {
  .el-aside {
    padding-top: 10px;
    border-right: 1px solid #dcdfe6;
  }
  .el-table--border {
    border-left: none;
  }
  .hed-btn {
    float: right;
    padding: 0 10px 10px 0;
  }
  .el-main {
    padding-bottom: 0;
  }
}
</style>



